


<template>
  
  <transition name="fade">
<ModalTest @closeModal="modalOpen = false" :oneRooms="oneRooms" :pressed="pressed" :modalOpen="modalOpen" />
</transition>



  <div class="menu">
    <!-- <a v-for="(d,i) in 메뉴들" :key="i">{{d}}</a> -->
      <h1 class="logo">Room info</h1> 
  </div>

  <DiscountTest v-if="showDiscount == true" />

  <div class="bottons">
    <button class="btn btn-outline-secondary sortButton" @click="basicSort">ABC순정렬</button>
    <button class="btn btn-outline-secondary sortButton" @click="pricedownSort">가격낮은순정렬</button>
    <button class="btn btn-outline-secondary sortButton" @click="priceupSort">가격높은순정렬</button>
    <button class="btn btn-outline-secondary sortButton" @click="pricelimitSort">50만원이하</button>
    <button class="btn btn-outline-secondary sortButton" @click="sortBack">되돌리기</button>
  </div>

 <CardTest @openModal="modalOpen = true; pressed = $event" :oneRooms="oneRooms[i]" v-for="(d, i) in oneRooms" :key="d.id" />
 <!-- <CardTest :원룸="원룸들[1]"/>
 <CardTest :원룸="원룸들[2]"/>
 <CardTest :원룸="원룸들[3]"/>
 <CardTest :원룸="원룸들[4]"/>
 <CardTest :원룸="원룸들[5]"/> -->


</template>
<script>
import data from './assets/oneroom.js';
import DiscountTest from './DiscountTest.vue';
import ModalTest from './ModalTest.vue';
import CardTest from './CardTest.vue';




export default {
  name: 'App',
  data(){
    return {
      showDiscount : true,
      oneRoomsOrigin : [...data],
      pressed : 0,
      oneRooms : data,
      modalOpen : false,
      신고수: [0, 0, 0],
      // 메뉴들 : ['Home', 'Shop', 'About'],
      products : ['역삼동원룸', '천호동원룸', '마포구원룸'],
    }
  },
  methods : {
    increase(){
      this.신고수[0] += 1;
    },
    sortBack(){
      this.oneRooms = [...this.oneRoomsOrigin];
    },
    basicSort(){
      this.oneRooms = [...this.oneRoomsOrigin].sort(function(a, b) {
      if (a.title < b.title) return -1;
      if (a.title > b.title) return 1;
      return 0;
  });
    },
    pricedownSort(){
      this.oneRooms = [...this.oneRoomsOrigin].sort(function( a, b ){
        return a.price - b.price
      })
    },
    priceupSort(){
      this.oneRooms = [...this.oneRoomsOrigin].sort(function( a, b ){
        return b.price - a.price
      })
    },
    pricelimitSort(){
    this.oneRooms = [...this.oneRoomsOrigin].filter(function(a) {
    return a.price <= 50;
     })
     .sort(function(a, b) {
      return a.price - b.price; 
    });
    }
  },

  mounted(){
    setTimeout(()=>{
      this.showDiscount = false;
   }, 2000);
  },

  watch: {
    modalOpen(val) {
      if (val) {
        document.body.classList.add('overflow-hidden'); 
      } else {
        document.body.classList.remove('overflow-hidden'); 
      }
    },
  },

  components: {
    DiscountTest,
    ModalTest,
    CardTest,
  }
}
</script>

