<template>
  <div class="black-bg" v-if="modalOpen == true" >
    <div class="white-bg">
      <img :src="oneRooms[pressed].image" style="width:100%">
      <h4>{{oneRooms[pressed].title}}</h4>
      <p>{{oneRooms[pressed].content}}</p>
      <!-- <input @input="month = $event.target.value"> -->
      <input v-model.number="month">
      <p> {{ month }}개월 선택함 : {{oneRooms[pressed].price * month}}만원</p>
      
      <DiscountTest/>
      <button @click="closeSend">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
    name : 'ModalTest',
    data(){
      return{
        month : 1,
      }
    },
    watch : {
      month(a){
        if(a >= 13){
          alert('13이상 입력하지 마세요')
        }
        if (isNaN(a) == true){
        alert('숫자를 입력하세요');
        this.month = 1;
        }
      }
      
    },
    props : {
      oneRooms : Array,
      pressed : Number,
      modalOpen : Boolean,
    },
    methods : {
      closeSend(){
        this.$emit('closeModal')
      }
    }
}
</script>

